
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import { getSingleUserInfo } from '@/api/base'
export default {
components: {
XHeader,
Group,
Cell,
XDialog
},
data () {
return {
showScrollBox: false,
bankCode: getSingleUserInfo('user_bankCode') || '未设置',
bankCardholder: getSingleUserInfo('user_bankCardholder') || '未设置',
bankName: getSingleUserInfo('user_bankName') || '未设置',
realName: getSingleUserInfo('user_realname') || '',
identityCode: getSingleUserInfo('user_identityCode') || ''
}
},
methods: {
alert: function () {
this.showScrollBox = true
},
gotoChangeCard: function () {
this.$router.push({path: '/changeIdCard'})
}
}
}



